.modal_overlay {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 60%);
  cursor: pointer;
}

.container {
  position: fixed;
  inset: 0;
  padding: 40px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
}
